import { FormControlLabel, Grid, Switch } from "@material-ui/core"
import { FormDropdown, FormInput, FormSubmit, PageView, Text } from "components/YeyComponents"
import React, { useCallback, useEffect, useState } from "react"
import { useForm } from "react-hook-form"
import { useDispatch, useSelector } from "react-redux"
import { useHistory, useParams } from "react-router-dom"
import { loadAllCombos, loadCitiesFromState } from "redux/actions/combos"
import { createSchool, getSchool, updateSchool, clearAll } from "redux/actions/school"
import api from 'services/api'
import toast from "react-hot-toast"
import errorHandler from 'utils/errorHandler'

const EscolaForm = () => {
    const { handleSubmit, control, formState: { errors }, reset, setValue } = useForm()
    const schoolData = useSelector(state => state.school)
    const statesList = useSelector(state => state.combos.statesList),
        citiesList = useSelector(state => state.combos.citiesList)

    const dispatch = useDispatch()
    const params = useParams()
    const history = useHistory()

    const [switchActive, setSwitchActive] = useState(false)

    useEffect(() => dispatch(loadAllCombos()), [dispatch])

    useEffect(() => {
        if (params?.schoolId) {
            dispatch(getSchool(params.schoolId))
        } else {
            dispatch(clearAll())
        }
    }, [dispatch, params])

    useEffect(() => {
        if (schoolData?.stateId) {
            Object.entries(schoolData).forEach(([key, value]) => {
              if(key === 'isActive') {
                setSwitchActive(value)
              }
              setValue(key, value)
            })
            dispatch(loadCitiesFromState(schoolData?.stateId))
        } else {
            reset(schoolData)
        }
    }, [schoolData, setValue, dispatch, reset])

    const getCitiesByStateId = useCallback(state => {
        dispatch(loadCitiesFromState(state.value))
        setValue("stateId", state.value)
    }, [dispatch, setValue])

    const selectCity = useCallback(city => {
        setValue("cityId", city.value)
    }, [setValue])

    const handleSwitch = evt => {
      setSwitchActive(evt.target.checked)
      setValue("isActive", evt.target.checked)
    }

    const onSubmit = data => {
      delete data.createdAt
      delete data.updatedAt
      data.isActive = params?.schoolId ? data.isActive : true
      dispatch(params?.schoolId ? updateSchool(data) : createSchool(data, history.push))
    }

    const getCepChange = useCallback(state => {
        if (state && state.length === 9) {
            api.get(`common/cep/${state}`)
                .then(async response => {
                    const { data } = response

                    setValue('cep', data?.cep)
                    setValue('complement', data?.complement)
                    setValue('district', data?.district)
                    setValue('stateId', data?.state?.id)
                    await dispatch(loadCitiesFromState(data?.state?.id))
                    setValue('cityId', data?.city?.id)
                    setValue('street', data?.street)
                })
                .catch(err => toast.error(`Erro ao encontrar CEP: ${errorHandler(err)}`))
        }
    }, [dispatch, setValue])


    return (
        <PageView>
            <Text type='title' padding={{ top: '1em' }}>{params?.schoolId ? 'Editar' : 'Adicionar'} Escola</Text>
            <hr style={{ marginTop: '2em' }} />

            <form style={{ width: '100%', flex: 1 }} onSubmit={handleSubmit(onSubmit)}>
                {/* <Grid  direction="column" justify="flex-start" alignItems="flex-start" spacing={1} > */}
                <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={1}>
                    <Grid item xl={6} lg={6} md={6} sm={12} xs={12} direction="column" >
                        <FormInput
                            name='name'
                            label='Nome da Escola'
                            control={control}
                            errors={errors}
                            defaultValue={schoolData?.name}
                            required
                        />
                    </Grid>
                </Grid>

                <hr style={{ marginTop: '2em' }} />
                <Text fontWeight='bold' padding={{ top: '1em', bottom: '1em' }}>Endereo</Text>

                <Grid item xl={6} lg={6} md={12} sm={12} xs={12}>
                    <FormInput
                        name='zipcode'
                        label='CEP'
                        mask='cep'
                        control={control}
                        errors={errors}
                        defaultValue={schoolData?.zipcode}
                        getChangeValue={e => getCepChange(e)}
                        required
                    />
                </Grid>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={1}>
                    <Grid item xl={6} lg={6} md={6} sm={12} xs={12} direction="column" >
                        <FormInput
                            name='street'
                            label='Logradouro'
                            control={control}
                            errors={errors}
                            defaultValue={schoolData?.street}
                            required
                        />
                        <FormInput
                            name='number'
                            label='Nmero'
                            control={control}
                            errors={errors}
                            defaultValue={schoolData?.number}
                            required
                        />
                        <FormDropdown
                            placeholder='Pesquisa o estado...'
                            name='stateId'
                            label='Estado'
                            required
                            values={statesList}
                            control={control}
                            errors={errors}
                            getChangeValue={e => getCitiesByStateId(e)}
                            width="70%"
                        />
                    </Grid>
                    <Grid item xl={6} lg={6} md={6} sm={12} xs={12} direction="column" >
                        <FormInput
                            name='district'
                            label='Bairro/Distrito'
                            control={control}
                            errors={errors}
                            defaultValue={schoolData?.number}
                            required
                        />
                        <FormInput
                            name='complement'
                            label='Complemento'
                            control={control}
                            errors={errors}
                            defaultValue={schoolData?.street}
                        />
                        <FormDropdown
                            placeholder='Pesquisa a cidade...'
                            name='cityId'
                            label='Cidade'
                            required
                            control={control}
                            errors={errors}
                            values={citiesList}
                            getChangeValue={e => selectCity(e)}
                            width="70%"
                        />
                    </Grid>
                    {
                      params?.schoolId &&
                      <Grid item xl={6} lg={6} md={6} sm={12} xs={12} direction="column">
                        <FormControlLabel
                          style={{marginTop: '2em', border: '2px solid black', width: '65%', padding: '1em', borderRadius: '20px'}}
                          label="Escola Ativa"
                          labelPlacement="end"
                          control={<Switch
                            checked={switchActive}
                            onChange={e => handleSwitch(e)}
                            color="primary"
                            name="isActive"
                            control={control}
                          />}
                        />
                      </Grid>
                    }
                </Grid>
                <FormSubmit label="Salvar" padding={{ top: '2em' }} />
                {/* </Grid> */}
            </form>
        </PageView>
    )
}

export default EscolaForm
